
.menu .menu-body {
  position: fixed;
  top: 0;
  left: -250px;
  width: 250px;
  background-color: #293d5a;
  height: 100%;
  z-index: 7;
  transition: transform 300ms;
}
.menu .menu-body.show {
  transform: translateX(250px);
}
.menu .menu-body .menu-user {
  overflow: hidden;
  background-color: #023244;
}
.menu .menu-body .menu-user .menu-avatar {
  width: 100px;
  height: 100px;
  margin: 20px auto;
  border-radius: 50%;
  overflow: hidden;
}
.menu .menu-body .menu-user .menu-name {
  color: #a4d2e3;
  text-align: center;
  line-height: 40px;
}
.menu .menu-body .menu-list ul li .menu-icon {
  display: inline-block;
}
.menu .menu-body .menu-list ul li .menu-text {
  margin-left: 10px;
}
.menu .menu-body .menu-list ul li .link {
  display: block;
  line-height: 50px;
  position: relative;
  padding: 0 20px;
  color: #999;
}
.menu .menu-body .menu-list ul li .link:after {
  content: '';
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  border-top: 1px solid #a4d2e3;
  width: 100%;
}
.menu .menu-body .menu-list ul li .link.active {
  color: #a4d2e3;
}



