
html,
body,
#app {
  height: 100%;
  width: 100%;
}
#app {
  position: relative;
  font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
  font-size: 18px;
}
#body {
  position: absolute;
  padding-top: 50px;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 4;
  -webkit-overflow-scrolling: touch;
  overflow: auto;
}
.overflow-h {
  overflow: hidden !important;
}
.clear-both:after {
  content: '';
  display: block;
  clear: both;
}
a {
  color: #333;
}
.fade-enter-active {
  transition: all .3s linear;
}
.fade-leave-active {
  transition: all .3s linear;
}
.fade-enter,
.fade-leave-to {
  transform: translateX(100%);
}



